/* ================================= 
  ELEMENT STYLES
==================================== */

* {
  box-sizing: border-box;
}
body {
  color: #576366;
  background:rgb(15, 1, 53);
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.name {
  font-size: 5em;
}
.content {
  color: white;
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow: auto;
  padding-left: 5em;
  padding-top: 5em;
  overflow: hidden;
}

.image {
  flex: 1 1;
  width: 25%;
}

.text {
  display: flex;
  flex-direction: column;
}
.linkText:hover {
  cursor: pointer;
  text-decoration: underline;
}

.listItem {
  padding-top: 1em;
}


